* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

img, picture, video, iframe, figure {
  display: block;
  max-width: 100%;
}

li {
  list-style-type: none;
}

html {
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p, span, a, strong, blockquote, i, b, u, em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

::selection {
  background-color: #eb4242;
  color: #fff;
}

form, input, textarea, select, button, label {
  display: block;
  font-family: inherit;
  font-size: inherit;
  hyphens: auto;
  background-color: transparent;
  color: inherit;
}
form:focus-visible, input:focus-visible, textarea:focus-visible, select:focus-visible, button:focus-visible, label:focus-visible {
  outline: none;
}

table, tr, td {
  border-collapse: collapse;
  border-spacing: 0;
}

svg {
  fill: currentColor;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-size: 100%;
  font-family: "Quicksand", sans-serif;
  color: #202124;
  background-color: #f9f9f9;
}
body #content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  ::-webkit-scrollbar {
    width: 0.5rem;
  }
  ::-webkit-scrollbar-thumb {
    background-color: #fcbbbb;
  }
  body #content {
    justify-content: center;
    align-items: center;
    padding: 2rem 0;
  }
}
.swiper {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-pagination-bullet {
  background: #eb4242 !important;
}

.categories {
  position: fixed;
  bottom: 0;
  height: auto;
  color: #fff;
  background: #eb4242;
  padding: 1em;
}
.categories .swiper-slide {
  width: auto;
  font-weight: 600;
}

.header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  color: #fff7f7;
  background: #e60023;
  padding: 0.5em 1em;
  height: 60px;
}
.header svg {
  font-size: 1.5rem;
}
.header .menu {
  display: flex;
  flex-flow: column nowrap;
  justify-content: start;
  align-items: center;
  position: fixed;
  top: 0;
  left: -100%;
  width: 100vw;
  height: 100vh;
  padding: 1em;
  background: #eb4242;
  transition: left 200ms ease-in-out;
  z-index: 2;
}
.header .menu svg {
  align-self: start;
}
.header .menu .links {
  display: flex;
  flex-flow: column nowrap;
  justify-content: start;
  align-items: center;
  gap: 0.5rem;
  text-align: center;
  margin: auto;
  padding: 0 1rem;
  overflow-y: scroll;
}
.header .menu .links .link {
  font-size: 1.25rem;
  animation: fade_in 200ms ease-in;
}
.header .menu.open {
  left: 0%;
}
.header .logo-container {
  flex-grow: 1;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
.header .logo-container .logo h1 {
  font-size: 1.15rem;
  font-weight: 500;
}
.header .search {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  position: fixed;
  top: 0;
  right: -100%;
  width: 100%;
  padding: 0.5em 1em;
  height: 60px;
  background: #eb4242;
  transition: right 200ms ease-in-out;
}
.header .search.open {
  right: 0%;
}
.header .search input {
  flex-grow: 1;
  background: #fff;
  color: #202124;
  padding: 0.5em 1em;
}